import { Button as ButtonType } from "@/types";
import { Button, Em, Flex, Heading, Link, Text } from "@radix-ui/themes";
import { ReactNode } from "react";
import { Responsive } from "@radix-ui/themes/props";

export default function HeadingAndContent({
  heading,
  headingSize = "8",
  subheading,
  subheadingSize = "3",
  buttons,
  buttonsFlow = "column",
  children,
  align = "left",
}: Readonly<{
  heading: string[];
  headingSize?: Responsive<"1" | "2" | "3" | "4" | "5" | "6" | "7" | "8" | "9">;
  subheading: string;
  subheadingSize?: Responsive<
    "1" | "2" | "3" | "4" | "5" | "6" | "7" | "8" | "9"
  >;
  buttons?: ButtonType[];
  buttonsFlow?: "row" | "column";
  children?: ReactNode;
  align?: "left" | "center" | "right";
}>) {
  const flexAlignCenterOrRight = align === "center" ? "center" : "end";
  const flexAlign = align === "left" ? "start" : flexAlignCenterOrRight;

  const buttonsBlock = (
    <Flex direction={buttonsFlow} gap="4" align={flexAlign}>
      {buttons?.map((button, index) => {
        return (
          <Button
            key={button.url}
            asChild={!!button.url}
            size={button.size ?? "3"}
            onClick={button.onClick}
            className="w-fit"
            variant={index === 0 ? "solid" : "outline"}
          >
            {button.url ? (
              <Link href={button.url} underline="none">
                {button.icon}
                {button.text}
              </Link>
            ) : (
              <>
                {button.icon} {button.text}
              </>
            )}
          </Button>
        );
      })}
    </Flex>
  );

  return (
    <Flex
      direction="column"
      gap="6"
      align={flexAlign}
      flexBasis="0"
      flexGrow="1"
    >
      <Flex direction="column" gap="4" align={flexAlign}>
        <Heading as="h1" size={headingSize}>
          {heading.map((part, index) => {
            if (part === "\n") {
              return <br key={"br" + index} />;
            }

            return index % 2 === 0 ? part : <Em key={part + index}>{part}</Em>;
          })}
        </Heading>
        <Text align={align} size={subheadingSize}>
          {subheading}
        </Text>
      </Flex>
      {children}
      {buttons && buttonsBlock}
    </Flex>
  );
}
